html {
	box-sizing: border-box;
}
*,
*::before,
*::after {
	box-sizing: inherit;
}
body {
	font-family: $normal-font, -apple-system, BlinkMacSystemFont, 'Segoe UI',
		Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
	font-weight: normal;
	background-color: #262626;
	background-image: url(../img/backgound.png);
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
	color: #fff;
}
.clearfix::after {
	content: '';
	display: table;
	clear: both;
}
img {
	display: block;
	font-weight: 900;
	max-width: 100%;
	height: auto;
	margin: 0;
	padding: 0;
}

main {
	position: relative;
	top: -120px;
	box-shadow: 0px 50px 120px 0px rgba(0, 0, 0, 0.004);
}

p {
	font-size: 19px;
	line-height: 38px;
}

.show {
	opacity: 1 !important;
	visibility: visible !important;
}
.hidden {
	opacity: 0 !important;
	visibility: hidden !important;
}
