.btn {
	&-primary {
		display: flex;
		align-items: center;
		justify-content: center;
		align-content: center;
		text-transform: uppercase;
		font-size: 14px;
		line-height: 1;
		letter-spacing: 0.6em;
		color: #fff;
		text-decoration: none;
		cursor: pointer;
	}
	&-size-1 {
		padding: 25px 70px;
		min-width: 250px;
		width: fit-content !important;
	}
	&-red {
		border-style: solid;
		border-width: 3px;
		border-color: rgb(255, 79, 0);
		background-color: rgba(255, 79, 0, 0.102);
		box-shadow: 0px 20px 50px 0px rgba(0, 0, 0, 0.25);
		transition: 0.3s all;
		&:hover,
		&:active {
			box-shadow: 0px 20px 50px 0px rgba(255, 79, 0, 0.102);
		}
	}
}

.btn-scroll-down {
	display: none;
	@include _mobile-tablet {
		bottom: 60px;
		left: 50%;
		transform: translateX(-50%);
		display: flex;
		align-items: center;
		justify-content: center;
		align-content: center;
		position: absolute;
		width: 45px;
		height: 25px;
		svg {
			width: 100%;
			height: 100%;
			color: $orange-color;
		}
	}
}
