.description {
	padding-top: 60px;
	padding-left: 60px;
	padding-right: 140px;
	margin-bottom: 70px;
	&__caption {
		font-size: 16px;
		line-height: 1;
		font-family: $semibold-font;
		color: rgb(145, 145, 145);
		letter-spacing: 0.6em;
		margin-bottom: 40px;
		.orange {
			color: $orange-color;
		}
	}
	&__title {
		position: relative;
		margin-bottom: 60px;
		margin-top: 0;
		&::before {
			content: '';
			width: 7px;
			height: 100%;
			left: -64px;
			position: absolute;
			background-color: $orange-color;
		}
	}
	&__text {
		display: block;
		p:first-child {
			margin: 0;
		}
		p span {
			font-family: $bold-font;
		}
	}
	.btn-primary {
		margin-top: 60px;
	}
	@include _mobile-tablet {
		display: none;
	}
}
