.header {
	position: relative;
	padding-top: 60px;
	z-index: 2;
	.container {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		align-content: space-between;
		padding-left: 60px;
		padding-right: 60px;
	}
	&-logo {
		width: 415px;
		height: 60px;
	}
	&-search {
		margin-right: 45px;
		cursor: pointer;
		position: relative;
		&__icon {
			width: 30px;
			height: 30px;
			svg {
				width: 100%;
				height: 100%;
				color: $orange-color;
			}
		}
		&-form {
			position: absolute;
			right: 0;
			display: flex;
			opacity: 0;
			visibility: hidden;
			align-items: center;
			justify-content: flex-start;
			transition: 0.3s all;
			top: -13px;
			&__input {
				margin-right: 20px;
				input {
					background-color: rgba(255, 255, 255, 0.3);
					backdrop-filter: blur(6px);
					outline: none;
					border: none;
					padding: 10px;
					color: #fff;
				}
			}
			&__btn {
				background-color: transparent;
				padding: 0;
				border: none;
				outline: none;
				margin: 0;
				cursor: pointer;
				svg {
					width: 30px;
					height: 30px;
					color: $orange-color;
				}
			}
		}
	}
	&-actions {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		align-content: flex-start;
	}
	&-menu {
		&__list {
			position: absolute;
			z-index: 5;
			list-style: none;
			left: 0;
			top: 0;
			margin: 0;
			background-color: rgba(38, 38, 38, 0.8);
			width: 100%;
			display: flex;
			align-items: center;
			justify-content: space-around;
			backdrop-filter: blur(6px);
			padding: 60px;
			transform: translateY(-100%);
			transition: 0.3s all;
			li a {
				color: #fff;
				text-decoration: none;
				text-transform: uppercase;
				font-weight: bold;
				transition: 0.3s all;
				&:hover {
					color: $orange-color;
				}
			}
		}
		&__btn {
			display: flex;
			align-items: flex-end;
			justify-content: flex-end;
			flex-direction: column;
			position: relative;
			z-index: 10;
			width: 46px;
			height: 20px;
			cursor: pointer;
			span {
				display: block;
				background-color: $orange-color;
				height: 2px;
				margin-bottom: 8px;
				width: 46px;
				transition: 0.3s all;
				&:last-child {
					width: 36px;
				}
			}
		}
		.active {
			span {
				position: absolute;
				&:nth-child(1) {
					// width: 30px;
					width: 36px;
					top: 50%;
					transform: rotate(135deg);
				}
				&:nth-child(2) {
					// width: 30px;
					top: 50%;
					transform: rotate(-135deg);
				}
			}
		}
	}
	&-backdrop {
		position: fixed;
		display: none;
		height: 100%;
		left: 0;
		z-index: 2;
		right: 0;
		bottom: 0;
		top: 0;
		width: 100%;
		background: rgba(38, 38, 38, 0.8);
		backdrop-filter: blur(2px);
	}
	@include _mobile-tablet {
		.container {
			flex-wrap: wrap;
		}
		&-logo {
			width: 100%;
			max-width: 600px;
			margin-bottom: 10px;
			margin-right: auto;
			margin-left: auto;
		}
		&-search {
			display: none;
			opacity: 0;
			visibility: hidden;
		}
		&-actions {
			margin-right: auto;
			margin-left: auto;
		}
		&-menu {
			&__list {
				padding: 10px;
				flex-direction: column;
				max-width: 220px;
				height: 100vh;
				position: fixed;
			}
		}
	}
	@include _mobile {
		padding-top: 30px;
		&-menu {
			&__btn {
				z-index: 2;
				span {
					width: 46px;
					&:last-child {
						width: 46px;
					}
				}
			}
			.active {
				span {
					width: 46px !important;
					position: static;
					transform: none !important;
				}
			}
		}
	}
	@include _tablet {
		padding-top: 50px;
	}
}
