@mixin _mobile {
	@media (max-width: 767px) {
		@content;
	}
}

@mixin _tablet {
	@media (min-width: 768px) and (max-width: 1199px) {
		@content;
	}
}
@mixin _mobile-tablet {
	@media (max-width: 1199px) {
		@content;
	}
}

@mixin _laptop {
	@media (min-width: 1200px) and (max-width: 1599px) {
		@content;
	}
}
