.carousel {
	max-width: 900px;
	width: calc(100% + 180px);
	position: relative;
	left: -180px;
	&::before {
		content: '';
		position: absolute;
		height: 155px;
		width: calc(100% - 60px);
		bottom: -60px;
		left: 120px;
		background-color: $orange-color;
		box-shadow: -1px -100px 65px 20px rgba(255, 79, 0, 0.04);
	}
	&-nav {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		align-content: flex-start;
		padding-left: 60px;
		margin-bottom: 55px;
		&__btn {
			display: flex;
			align-items: center;
			justify-content: center;
			padding: 0;
			cursor: pointer;
			width: 60px;
			height: 18px;
			background-color: transparent;
			border: none;
			outline: none;
			margin-right: 60px;
			&:last-child {
				margin-right: 0;
			}
			svg {
				width: 100%;
				height: 100%;
				color: $orange-color;
			}
		}
		.next {
			svg {
				transform: rotate(180deg);
			}
		}
	}
	.owl-dots {
		position: absolute;
		display: flex;
		align-items: flex-end;
		justify-content: flex-start;
		align-content: flex-start;
		flex-direction: column;
		top: -350px;
		right: 0;
		.owl-dot {
			background-color: rgb(119, 119, 119) !important;
			width: 15px;
			height: 1px;
			margin-bottom: 20px;
			transition: 0.3s all;
		}
		.active {
			width: 30px;
			height: 2px;
			background-color: $orange-color !important;
		}
	}
	@include _mobile-tablet {
		display: none;
		&-nav {
			display: none;
		}
	}
	@include _laptop {
		// width: 100%;
		left: -120px;
		&::before {
			left: 60px;
		}
	}
}

.owl-stage-outer {
	padding: 30px 20px;
	.owl-item {
		opacity: 0;
		visibility: hidden;
		transition: 0.3s all;
	}
	.active {
		opacity: 1;
		visibility: visible;
	}
}
.carosuel-item {
	width: 300px;
	transition: 0.3s all;
	position: relative;
	transition: 0.3s all;
	&::before {
		content: '';
		width: 0;
		height: 10px;
		background-color: $orange-color;
		opacity: 0;
		visibility: hidden;
		position: absolute;
		transition: 0.3s all;
		top: -10px;
	}
	&:hover {
		z-index: 2 !important;
		box-shadow: 0px 0px 10px 10px rgba(0, 0, 0, 0.35);
		transform: translateY(-10px);
		&::before {
			width: 100%;
			opacity: 1;
			visibility: visible;
		}
	}
	@include _laptop {
		width: auto;
	}
}
