.row {
	display: flex;
	justify-content: stretch;
	align-items: stretch;
	flex-wrap: wrap;
	align-content: stretch;
}
.col-6 {
	width: 50%;
	padding: 60px;
	display: flex;
	flex-direction: column;
	align-items: stretch;
	// justify-content: stretch;
	// align-content: stretch;
	@include _mobile-tablet {
		width: 100%;
		padding: 50px;
	}
}
.col_no-padding {
	@include _mobile-tablet {
		padding: 0;
	}
}
